.appointment__details {
  display: flex;
  justify-content: space-between;

  @include for-small-desktop-up {
    display: grid;
    grid-gap: 1rem;
    justify-content: center;
    justify-items: center;
  }

  &--info {
    display: grid;
  }
}
.appointment__header {
  position: relative;
  padding: 0.3rem;
  border-radius: 0.5rem;
  overflow: hidden;
}
.appointment--closeBtn {
  position: absolute;
  top: 1rem;
  right: 1rem;
}
