@import "../abstracts/RFS";
// =====================================//

// MESSAGE TAB
.tab__message {
  display: grid;
  grid-template-columns: auto 1fr;
  margin: 1rem 0;
  grid-gap: 0.5rem;
  height: 95%;
  max-height: 30rem;

  &--messages {
    justify-items: center;
    grid-gap: 1rem;
    background-color: $white;
    max-height: 60rem;

    overflow: scroll;
    overflow-x: hidden;

    &::-webkit-scrollbar {
      width: 0.5rem;
      border-radius: 1rem;
    }
    &::-webkit-scrollbar-track {
      background-color: $light-gray;
    }
    &::-webkit-scrollbar-thumb {
      background-color: $blue;
      border-radius: 1rem;
    }
    .message--wrapper {
      position: relative;
      width: 28rem;
      overflow: hidden;

      @include for-tablet-landscape-up {
        width: auto;
      }
      :hover {
        background-color: $light-gray;
      }
    }
    .message {
      margin: 0.5rem;
      background-color: $white;
      display: grid;
      grid-template-columns: max-content 1fr;
      align-items: center;
      padding: 0 1rem;
      border-radius: 1rem;
      height: 6rem;
      overflow: hidden;
      position: relative;
      // MESSAGE CARD ACTIVE STATE
      &--active {
        ::after {
          content: "";
          position: absolute;
          top: 0;
          right: 0;
          width: 0.5rem;
          height: 100%;
          background-color: $blue;
        }
      }
      // MESSAGE CARD ACTIVE STATE

      // MESSAGE CARD COMPONENTS
      &__avatar {
        width: 4.5rem;
        height: 4.5rem;
        border-radius: 50%;
        overflow: hidden;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &__content {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      &__sender {
        margin-left: 0.8rem;
        max-width: 80%;
        @include for-tablet-landscape-up {
          display: none;
        }
      }
      &__account {
        color: $blue;
        @include font-size(1.5rem);
      }
      &__snippet {
        max-width: 14rem;
        height: 1rem;

        font-weight: 600;
        overflow: hidden;
        @include font-size(0.8rem);
      }
      &__date {
        color: $dark-gray;

        @include font-size(0.9rem);
        @include for-tablet-landscape-up {
          display: none;
        }
      }
    }
  }

  &--conversation {
    max-height: 60rem;
    display: flex;
    background-color: $white;
    .conversation {
      flex-basis: 100%;
      display: flex;
      flex-direction: column;
      padding: 0 1rem;

      &__header {
        flex-basis: 10%;
        padding: 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid $light-gray;

        &--actions {
          color: $blue;

          :hover {
            color: $dark-blue;
          }
        }
      }
      &__body {
        flex-basis: 80%;
        min-height: 10rem;
        margin: 1rem 0;
        border-radius: 1rem 0 0 1rem;
        background-color: $white;
        overflow: scroll;
        overflow-x: hidden;
        display: flex;
        flex-direction: column;
        padding: 0.5rem;

        &::-webkit-scrollbar {
          width: 0.5rem;
          border-radius: 1rem;
        }
        &::-webkit-scrollbar-track {
          background-color: $light-gray;
        }
        &::-webkit-scrollbar-thumb {
          background-color: $blue;
          border-radius: 1rem;
        }
      }
      &__footer {
        flex-basis: 10%;
        display: flex;

        min-height: 2rem;
        max-height: 4rem;
        .reply__input {
          flex: 1 1 80%;
          resize: none;
          border: 1px solid $blue;
          border-radius: 0.5rem 0 0 0.5rem !important;
          padding: 1rem;
          @include font-size(1.2rem);
          font-weight: 600;
          color: $blue;

          font-family: $font-primary;
          &::placeholder {
            color: $blue;
          }
          &:focus {
            border-radius: 0.5rem 0 0 0.5rem !important;
          }
          &::-webkit-scrollbar {
            display: none;
          }
          &::-webkit-scrollbar-track {
            background-color: $light-gray;
          }
          &::-webkit-scrollbar-thumb {
            background-color: $blue;
            border-radius: 1rem;
          }
        }
        .reply__btn {
          flex: 1 1 20%;
          background-color: $blue;
          border: 1px solid $blue;
          border-radius: 0 1rem 1rem 0 !important;
          color: $white;
          width: 100%;
          padding: 0.5rem;
          :focus {
            background-color: $blue;
          }
        }
      }
    }
  }
}

.sender {
  position: relative;

  &__name {
    color: $blue;
    display: flex;
    &:before {
      content: "From:";
      font-weight: bold;
      padding-right: 0.5rem;
    }
  }
}

.sender--bubble {
  display: flex;
}
.sender--reply {
  max-width: 65%;
  margin-left: 0.5rem;
  @include font-size(0.95rem);
  font-weight: 600;
  color: black;
  background-color: $light-gray;
  border-radius: 0.5rem;
  padding: 1rem;
}
.sender--avatar {
  position: relative;
  max-width: 3rem;
  max-height: 3rem;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 100%;
  }
}
.user--bubble {
  width: 100%;
  margin: 1rem 0;
  display: flex;
  justify-content: flex-end;
}
.user--reply {
  max-width: 65%;
  margin-left: 0.5rem;
  @include font-size(0.95rem);
  font-weight: 600;
  color: $white;
  background-color: $blue;
  border-radius: 0.5rem;
  padding: 1rem;
}
