@import "../abstracts/RFS";
// ===================================//
.calendar {
  width: 35rem;
  max-width: 100%;
  background-color: $blue;
  border-radius: 0.5rem;
  border: 2px solid $white;
  border-collapse: collapse;

  @include for-phone-only {
    width: 26rem;
  }

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $white;
  }
  &__content {
    border-collapse: collapse;
    width: 100%;
    padding: 0rem;
  }
  .btn-nextMonth,
  .btn-prevMonth {
    color: $blue;
  }
  .current--day {
    border-radius: 0.5rem;
    background-color: $white;
    color: $blue;
  }
}
.calendar--inverted {
  width: 35rem;
  max-width: 100%;
  background-color: $white;
  border-radius: 0.5rem;
  border: 1px solid $white;
  border-collapse: collapse;

  .calendar__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $blue;
    border: 1px solid $blue;
    .calendar--month {
      padding: 1rem;
      @include font-size(1.5rem);
      color: $white;
    }
  }
  .calendar__content {
    border-collapse: collapse;
    width: 100%;

    #calendar-body {
      padding: 2rem;
      tr {
        .calendar--day {
          padding: 1rem;
          @include font-size(1.5rem);

          text-align: center;
          color: $blue;

          &:hover {
            background-color: $blue;
            border-radius: 0.5rem;

            color: $white;
          }
        }
      }
    }
  }
  .btn-nextMonth,
  .btn-prevMonth {
    color: $white;
  }
  .current--day {
    border-radius: 0.5rem;
    background-color: $blue;
    color: $white;
  }
}

.calendar--day {
  padding: 1rem;
  @include font-size(1.2rem);
  //   background-color: pink;

  text-align: center;
  color: $white;

  &:hover {
    background-color: $white;
    border-radius: 0.5rem;

    color: $blue;
  }
}
.calendar--month {
  padding: 1rem;
  @include font-size(2rem);
  color: $blue;
}

.calendar--span {
  background-color: $light-gray;

  border-collapse: collapse;

  th {
    font-weight: normal;
    @include font-size(1.5rem);
    padding: 1rem;

    @include for-phone-only {
      padding: 0.5rem;
    }
  }
}
