@import "../abstracts/RFS";
// ===================================//

.buyer {
  display: grid;

  height: 100vh;
  grid-template-columns: [buyerSidebar]25vw [buyerContent]1fr;
  @include for-tablet-portrait-up {
    grid-template-columns: 1fr;
    grid-template-rows: [buyerSidebar]1fr [buyerContent]1fr;
  }
  &--image {
    overflow: hidden;
    position: relative;
    width: 15rem;
    height: 15rem;

    @include for-tablet-landscape-up {
      width: 10rem;
      height: 10rem;
    }

    img {
      display: block;
      width: 100%;
    }
  }
  &--sidebar {
    grid-column: buyerSidebar;
    position: fixed;
    top: 0;
    width: 25vw;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    @include for-tablet-portrait-up {
      position: relative;
      height: 25vw;
      width: 100%;
      grid-row: buyerSidebar;
    }
    .sidebar--content {
      flex: 1;
      width: 100%;
    }
  }

  &--menu {
    margin: 3rem 0;
    align-items: center;
    @include for-tablet-portrait-up {
      padding: 0;
      grid-template-columns: repeat(3, 1fr);
    }
  }
  &--action {
    text-decoration: none;
    vertical-align: middle;
    padding: 2rem;

    @include for-tablet-portrait-up {
      padding: 1rem;
    }
  }

  &--content {
    grid-column: buyerContent;

    display: flex;

    @include for-tablet-portrait-up {
      grid-row: buyerContent;
    }
  }
}

.image--overlay {
  position: absolute;
  bottom: 0;
  text-decoration: none;
  cursor: pointer;
  background-color: rgba(77, 77, 77, 0.562);
  width: 100%;
  height: 2rem;
  display: flex;
  align-items: center;
}
.buyer--messages,
.buyer--profile,
.buyer--savedProperties {
  display: none !important;
  margin-top: 8rem;
  padding: 0 1rem;

  width: 100%;
}
.buyer--savedProperties {
  max-height: 100vh;
}
.buyer--messages {
  height: 10rem;
}
.bContent--active {
  display: block !important;
}
.bMenu--active {
  background-color: $dark-blue;
}
