.booking-place-wrapper {
  display: grid;
  grid-template-rows: auto 1fr;
  overflow: hidden;
}
.header--placeholder {
  height: 8.35rem;
  position: relative;
  background-color: red;
}
.booking {
  &-hero-content {
    background-color: $white;
    overflow: hidden;

    position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .booking-place-img {
      display: grid;
      grid-template-columns: 3fr 1fr;
      grid-gap: 1rem;

      @include for-small-desktop-up {
        display: block;
      }

      .booking-bannerImg-1 {
        background-color: $white;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      .booking-bannerImg-2 {
        background-color: blue;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        @include for-small-desktop-up {
          display: none;
        }
      }
    }
    .booking-place-desc {
      padding: 3.5rem;
      position: absolute;
      bottom: 0;
      left: 0;

      @include for-tablet-landscape-up {
        padding: 1rem;
      }

      h2 {
        @include for-tablet-landscape-up {
          @include font-size(2.5rem);
        }
      }
    }
  }
  &-nav-wrapper {
    background-color: $blue;
    width: 100%;
    padding: 1.5rem;
  }
  &-nav {
    background-color: $white;

    width: 80%;
    max-width: 145rem;
    padding: 1.5rem;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 2fr 1fr;
    grid-gap: 1rem;
    align-items: center;

    select {
      height: 100% !important;
    }
  }
}
// BOOKING CARD DECK
.booking--cards {
  margin: 0 auto;
  width: 100%;

  display: flex;

  flex-wrap: wrap;
  & > * {
    flex-basis: 49%;
    margin: 0.5rem;
    overflow: hidden;
    background-color: $white;
    min-width: 0;

    @include for-reg-desktop-up {
      flex-basis: 100%;
    }
  }
}
.booking-place-content-wrapper {
  width: 95%;
  max-width: 145rem;
  padding-top: 1rem;
  margin: 0 auto;
}
.booking-place {
  &-content {
    display: flex;
    flex-wrap: wrap;
  }
  &-information {
    max-width: 85rem;
    width: 80%;
    flex-basis: 60%;
    padding-right: 1.5rem;

    & > * {
      margin-top: 1.5rem;
    }
  }
  &-recentReviews {
    flex-basis: 40%;
  }
}

.place {
  &-about {
    .place-aboutImage {
      position: relative;
      img {
        width: 100%;
      }
    }
    .place-about-name {
      color: $white;
      padding: 1rem 1.5rem;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}
