.timePicker {
  border-radius: 0.5rem;
  border: 2px solid $white;
  &__header {
    display: flex;
    align-items: center;
    background-color: $white;
    padding: 1rem;
    .timePicker--title {
      margin: auto;
      color: $blue;
    }
  }
  &__content {
    border-collapse: collapse;
    width: 100%;
    padding: 0rem;
  }
}
.time--title {
  padding: 1.5rem;
  @include font-size(1.2rem);
  //   background-color: pink;
  cursor: pointer;
  text-align: center;
  color: $white;

  &:hover {
    background-color: $white;
    border-radius: 0.5rem;

    color: $blue;
  }
}
.time--timeOfDay {
  padding: 1rem;
  @include font-size(1.3rem);
  font-weight: 600;
  color: $blue;
  background-color: $light-gray;
}
// AGENT CONTACT DIALOG
.agent__contact--wrapper {
  background-color: $white;
  width: 100%;
  display: flex;
  padding: 1rem;

  .agent__contact--inner {
    margin: auto;
    display: flex;
    align-items: center;

    .agent__image {
      width: 10rem;
      height: auto;
      img {
        width: 100%;
      }
    }
    .agent__info {
      transition: all 0.2s ease-in-out;
      margin-left: 1rem;
      * {
        margin: 0.5rem 0;
      }
      font-size: 1.5rem;
      font-weight: 300;
      :first-child {
        font-weight: 700;
        color: $blue;
        font-size: 2rem;
      }
      .info-email {
        position: relative;
        &::before {
          content: "Email Address:";
          margin-right: 0.3rem;
        }
      }
      .info-contact {
        position: relative;
        &::before {
          content: "Contact No:";
          margin-right: 0.3rem;
        }
      }
      @include for-phone-only {
        transition: all 0.2s ease-in-out;
        margin: auto;
        text-align: center;
      }
    }
  }
  .agent__contact--content {
    display: grid;
    grid-gap: 2rem;
    width: 100%;

    a {
      justify-self: center;
    }
  }
}

